.Title{
    font-size: 28px;
    font-weight: 700;
}

.List {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    display: flex;
    justify-content: center;
}

.Item:not(:last-child){
margin-right: 10px;
}

.Button{
    cursor: pointer;
    border: none;
    min-width: 100px;
    min-height: 25px;
    font-size: 16px;
    font-weight: 500;
    border-radius: 4px;
}